import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import SettingsIcon from '@material-ui/icons/Settings';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import EqualizerIcon from '@material-ui/icons/Equalizer';
import ListIcon from '@material-ui/icons/List';
import NoteAddOutlinedIcon from '@material-ui/icons/NoteAddOutlined';
const useStyles = makeStyles({
    root: {
        width: 500,
    },
});

export default function Botttom() {
    const classes = useStyles();
    const [value, setValue] = React.useState('recents');

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <BottomNavigation value={value} onChange={handleChange} className={classes.root}>
            <BottomNavigationAction label="Add New" value="add" icon={<NoteAddOutlinedIcon />} />
            <BottomNavigationAction label="Trans.." value="trans" icon={<ListIcon />} />
            <BottomNavigationAction label="Stats" value="stats" icon={<EqualizerIcon />} />
            <BottomNavigationAction label="Accounts" value="accounts" icon={<AccountBalanceIcon />} />
            <BottomNavigationAction label="Settings" value="settings" icon={<SettingsIcon />} />
        </BottomNavigation>
    );
}